  font-size: 16px;
  line-height: 18px;
  font-family: "PT Mono", "Roboto", sans-serif;
  color: #ffffff; }
  .header__email::before,
  .header__phone::before {
    content: "";
    position: absolute; }

.header__email {
  margin-right: 10px; }
  .header__email::before {
    top: 2px;
    left: 4px;
    width: 19px;
    height: 14px;
    background: #1b2d37 url("../../img/icon-email.svg") no-repeat 0 0;
    background-size: contain; }
  @media (max-width: 1023px) {
