      height: 19px; } }
  @media (max-width: 767px) {
    .header__email {
      margin-right: 0; } }

.header__phone {
  white-space: nowrap; }
  .header__phone::before {
    top: 0;
    left: 9px;
    width: 18px;
    height: 18px;
    background: #1b2d37 url("../../img/icon-phone.svg") no-repeat 0 0;
    background-size: contain; }
  @media (max-width: 1023px) {
    .header__phone::before {
      width: 24px;
      height: 24px; } }

.header__button {
