  padding-top: 160px;
  padding-bottom: 196px; }
  .preview::before {
    content: "";
    position: absolute;
    top: -98px;
    left: 0;
    z-index: -1;
    width: 100%;
    height: 100%;
    background: #1b2d37 url("../../img/preview-bg-desktop@1x.jpg") no-repeat 0 0;
    background-size: 100% 878px; }
  @media (max-width: 1023px) {
    .preview {
      padding-top: 32px;
      padding-bottom: 245px; }
      .preview::before {
        top: -82px;
        background-image: url("../../img/preview-bg-tablet@1x.jpg");
        background-size: 100% 686px; } }
  @media (max-width: 767px) {
    .preview {
      padding-bottom: 134px; }
      .preview::before {
        top: -64px;
        background-image: url("../../img/preview-bg-mobile@1x.jpg");
        background-size: 100% 647px; } }

.preview__container {
